.o-action-bar {
	background-color: #f5f5f5;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
}

.o-action-bar-avatar {
	width: 40px;
	height: 40px;
	margin: 8px 0 8px 16px;
	padding: 0;
}

.o-action-bar-title {
	font-size: 20px;
	font-weight: 500;
	line-height: 1.6;
	letter-spacing: 0.02em;
	line-height: 1;
	float: left;
	flex: 1;
	margin-bottom: 0;
	padding: 16px;
}

.o-action-bar-list {
	float: left;
	flex: 1;
	margin: 0;
	padding: 0;
	list-style: none;
}

.o-action-bar-list-item {
	display: flex;
	float: left;
	padding: 16px;
}

.o-action-bar-list-item:first-of-type {
	padding-right: 0;
}

.o-action-bar-actions {
	margin: 0;
	padding: 0;
	list-style: none;
}

.o-action-bar-action {
	display: flex;
	float: right;
}

.o-action-bar-action-item {
	position: relative;
}

.o-action-bar-action-link {
	color: #757575;
	display: inline-block;
	padding: 16px;
}

.o-action-bar-action-link:hover, .o-action-bar-action-link:focus {
	background-color: #eeeeee;
	color: #258cf4;
}

.o-action-bar-action-icon {
	margin-right: 0;
}

.o-action-bar-action-icon:hover {
	text-decoration: none;
}

.c-forum {
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
	background-color: #ffffff;
	border-radius: 2px;
	position: relative;
	display: flex;
	flex-direction: column;
	margin-bottom: 16px;
}

.c-forum-body {
	flex: 1 1 auto;
	margin-bottom: 16px;
}

.c-forum-media {
	display: flex;
	float: left;
	flex: 1;
	flex-direction: column;
	margin: 16px 16px 0;
}

.c-forum-media-icon {
	color: #e8306e;
	width: 40px;
	height: 40px;
	margin-right: 0;
}

.c-forum-media-icon-read {
	color: #258cf4;
}

.c-forum-info {
	display: flex;
	flex-direction: column;
	margin-right: 16px;
}

.c-forum-title {
	font-size: 24px;
	font-weight: 400;
	line-height: 1.3333333333;
	letter-spacing: normal;
	margin-top: 24px;
	margin-bottom: 12px;
}

.c-forum-title-link {
	line-height: 1;
}

@media (min-width: 992px) {
	.c-forum-title {
		margin-bottom: 0;
	}
}

.c-forum-text {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.4285714286;
	letter-spacing: 0.04em;
	margin: 16px 0 0;
}

.c-forum-footer {
	border-top: 1px solid rgba(0, 0, 0, 0.125);
	border-radius: 0 0 2px 2px;
}

.c-forum-cat-title {
	font-size: 24px;
	font-weight: 400;
	line-height: 1.3333333333;
	letter-spacing: normal;
	border-bottom: 1px solid rgba(0, 0, 0, 0.125);
	margin-top: 16px;
	margin-bottom: 24px;
	padding-bottom: 4px;
}

.c-forum-cat-title-link {
	line-height: 1;
}

@media (min-width: 992px) {
	.c-forum-deck {
		display: flex;
		flex-flow: row wrap;
	}
	.c-forum-deck .c-forum {
		display: flex;
		flex: 1 0 0%;
		flex-direction: column;
		min-width: calc((100% / 2) - (12px));
		max-width: calc((100% / 2) - (12px));
		margin-bottom: 24px;
		margin-left: 24px;
	}
	.c-forum-deck .c-forum:first-child, .c-forum-deck .c-forum:nth-child(2n+1) {
		margin-left: 0;
	}
}

