.c-menu-container {
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
	text-align: left;
	background-color: #ffffff;
	border-radius: 2px;
	position: absolute;
	z-index: 999;
	top: auto;
	right: 0;
	display: none;
	overflow: auto;
}

.c-menu {
	margin: 0;
	padding: 8px 0;
	list-style: none;
}

.c-menu-item {
	position: relative;
}

.c-menu-toggle {
	cursor: pointer;
}

.c-menu-toggle:hover > .c-menu-container,
.c-menu-toggle:hover a > .c-menu-container, .c-menu-toggle:focus > .c-menu-container,
.c-menu-toggle:focus a > .c-menu-container {
	display: block;
}

.c-menu-link {
	font-size: 14px;
	font-weight: 500;
	line-height: normal;
	letter-spacing: normal;
	white-space: nowrap;
	text-decoration: none;
	color: #212121;
	display: flex;
	flex-direction: row;
	align-items: center;
	height: 48px;
	padding: 0 16px 0 16px;
	cursor: pointer;
}

.c-menu-link-icon {
	color: #757575;
	width: 32px;
	height: 32px;
	margin-right: 24px;
}

.c-menu-link:hover {
	text-decoration: none;
	background-color: #eeeeee;
	color: #258cf4;
}

.c-menu-link:hover .c-menu-link-icon {
	color: #258cf4;
}

.c-menu-divider {
	background-color: rgba(0, 0, 0, 0.125);
	height: 1px;
	margin-top: 8px;
	margin-bottom: 8px;
}

.c-toolbar {
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
	background-color: #f5f5f5;
	border-radius: 2px;
	position: relative;
	display: inline-block;
	margin: 0;
	margin-bottom: 16px;
	padding: 0;
	list-style: none;
}

.c-toolbar-item {
	position: relative;
	display: inline-block;
}

.c-toolbar-action {
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 500;
	line-height: 1;
	letter-spacing: normal;
	line-height: 2.7142857143;
	text-align: center;
	vertical-align: middle;
	white-space: nowrap;
	text-decoration: none;
	outline: none;
	color: #757575;
	position: relative;
	display: inline-block;
	overflow: hidden;
	margin: 0;
	padding: 0 16px;
	cursor: pointer;
	user-select: none;
	transition: box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1), color 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.c-toolbar-action:hover, .c-toolbar-action:focus {
	background-color: #eeeeee;
	color: #258cf4;
}

.c-toolbar-icon {
	margin-right: 0;
}

.c-toolbar-icon:hover {
	text-decoration: none;
}

.c-paging {
	display: flex;
	margin: 0;
	padding: 0;
	list-style: none;
	align-content: center;
}

.c-paging-item {
	margin: 0;
}

.c-paging-link {
	font-size: 18px;
	line-height: 1.6666666667;
	border-radius: 2px;
	margin: 0;
	padding: 4px 8px;
}

.c-paging-link:hover {
	text-decoration: none;
	background: #eeeeee;
}

.c-paging-link-icon {
	width: 24px;
	height: 24px;
	margin: 0;
}

.c-paging-item:first-of-type .c-paging-link,
.c-paging-item:last-of-type .c-paging-link {
	padding: 4px;
}
